@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

span {
  font-family: "Roboto", sans-serif;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.system-panel {
  width: 150px;
  height: 40px;
  padding: 16px 15px 14px 15px;
  background-color: #101022;
  display: flex;
}

.system-panel__button {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 10px;
}

.system-panel__button--close {
  background-color: #ff594f;
}

.system-panel__button--minimize {
  background-color: #ffbc1b;
}

.system-panel__button--maximize {
  background-color: #00cd46;
}

.sidebar__item {
  width: 150px;
  height: 50px;
  display: flex;
  padding: 15px 17px;
  align-items: center;
  border: 0px;
  background-color: transparent;
  cursor: pointer;
}

.sidebar__item:hover {
  background-color: #292a48;
}

.sidebar__item--active {
  background-color: #292a48;
}

.sidebar__item__icon {
  height: 16px;
  width: 16px;
  margin-right: 9px;
}

.sidebar__item__title {
  font-size: 14px;
  color: #fff;
}

.sidebar__item__number {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  color: #8a8995;
  text-align: right;
  width: 100%;
}

.issue {
  background-color: #fff;
  text-align: left;
  margin-bottom: 5px;
  height: 40px;
  width: 465px;
  padding: 12px;
  display: flex;
  align-items: center;
  border-width: 1px;
  border-color: rgb(224, 224, 224);
  border-style: solid;
  border-radius: 5px;
}

.issue__title {
  font-size: 15px;
  color: #080606;
}

.issue:nth-child(2) {
  margin-top: 10px;
}

.issue:last-child {
  margin-bottom: 10px;
}

.issue__button {
  border: 0;
  background: transparent;
  white-space: nowrap;
  text-decoration: none;
  cursor: pointer;
  padding: 0px;
  margin-left: auto;
}

.issue__button__star {
  width: 15px;
  height: 14px;
  margin-left: auto;
}

.issue-group {
  margin: 0px 20px 0px 15px;
  text-align: left;
}

.issue-group__date {
  font-family: "Roboto";
  font-size: 12px;
  color: #8a8995;
}

.sidebar {
  width: 150px;
  height: 500px;
  background-color: rgba(16, 16, 34, 0.9);
  position: relative;
  z-index: 3;
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
}

.content {
  width: 500px;
  height: 500px;
  background-color: #f6f6f6;
  overflow-y: auto;
  padding: 11px 0px;
}

.panel {
  display: flex;
  box-shadow: 0 0 15px 0px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  overflow: hidden;
}

.panel-container {
  width: 1000px;
  height: 1000px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(/el_passion_assignment/static/media/background.0f70c6b9.jpg);
  background-position: center;
}

